import React, { useEffect } from "react";
import { Outlet, useLocation, useNavigate } from "react-router-dom";

import Breadcrumb from "./components/Breadcrumb";
import { APP_ROUTES, COOKIE } from "./constants/app";
import { deleteCookie, getCookie } from "./helpers/cookie";

function App() {
  const navigate = useNavigate();
  const location = useLocation();
  const visitedPage = getCookie(COOKIE.VISITED_PAGE);

  useEffect(() => {
    if (visitedPage) {
      deleteCookie(COOKIE.VISITED_PAGE);
      // remove any trailing slash "/" and navigate
      navigate(visitedPage.replace(/\/?$/, ""));
    }
    // Run only once in App's lifetime
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (visitedPage) {
    // Avoid FOMW (Flash Of My Workaround) for direct URL hits
    return null;
  }

  return (
    <>
      {location.pathname !== APP_ROUTES.HOME && (
        <Breadcrumb path={location.pathname} />
      )}
      {/* Outlet for child routes */}
      <Outlet />
    </>
  );
}

export default App;
